body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.spinner {
  width: 50px;
  height: 50px;
  background: rgba(7, 111, 170, 1);
  border-radius: 50%;
  position: relative;
  margin: 20px;
  display: inline-block;
  box-shadow: 0 0px 0 0px rgba(7, 111, 170,0.5);
}

.spinner:after, .spinner:before {
  content: "";
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.spinner-1:after {
  position: absolute;
  top: -15px;
  left: -15px;
  border: 4px solid transparent;
  border-top-color: rgba(7, 111, 170,0.5);
  border-right-color: rgba(7, 111, 170,0.5);
  border-left-color: rgba(7, 111, 170,0.5);
  border-bottom-color: rgba(7, 111, 170,1);

  -webkit-animation: spinny 1s linear infinite;
  animation: spinny 1s linear infinite;
}

@-webkit-keyframes spinny {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
  }



  100% {
    -webkit-transform: rotate(360deg) scale(1);
    transform: rotate(360deg) scale(1);
  }
}

@keyframes spinny {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
  }



  100% {
    -webkit-transform: rotate(360deg) scale(1);
    transform: rotate(360deg) scale(1);
  }
}

.Card {
  background: #76b3cd;
}

html {
  line-height: normal;
}

*, *::after, *::before {
  box-sizing: border-box;
}

